@import "~@/assets/css/_variables.scss";

#addNewForm {
  display: flex;
  flex-wrap: wrap;
  > div {
    display: flex;
    flex-wrap: wrap;
    padding: 4px;
    width: 100%;
    > label,
    > span {
      width: 100%;
      font-size: $fs * 0.9;
      margin-bottom: 4px;
    }
    input {
      width: 100%;
      border-radius: 8px;
      border: 1px solid #ccc;
      padding: 8px;
      background-color: #fff;
      &[readonly] {
        font-weight: bold;
        background: none;
        border: none;
        padding: 0;
      }
      &[type="radio"] {
        display: none;
        & + label {
          width: auto;
          cursor: pointer;
          background-color: #fff;
          border-radius: 99px;
          border: 1px solid #ccc;
          padding: 8px 16px;
          font-size: $fs;
          margin-right: 8px;
          transition: background-color 0.2s, color 0.2s;
        }
        &:checked + label {
          background-color: $c-main;
          color: #fff;
        }
      }
      &[type="file"] {
        flex-grow: 1;
        padding: 4px;
        z-index: 0;
        & + button {
          flex-shrink: 0;
          margin-left: 8px;
          width: auto;
          border-radius: 8px;
          border: 1px solid #ccc;
          padding: 0 8px;
          z-index: 1;
          background-color: #fafafa;
          transition: background-color 0.2s;
          &:hover {
            background-color: #eee;
          }
          i {
            font-size: $fs;
          }
        }
      }
    }
    select {
      &[disabled] {
        color: #ccc;
        background-color: #eee;
      }
    }
    .input-file {
      display: flex;
      width: 100%;
    }
    > select {
      width: 100%;
      background: url("../assets/icon/arrow-down.png") no-repeat 94% / 14px #fff;
      appearance: none;
      border-radius: 8px;
      border: 1px solid #ccc;
      padding: 8px;
      color: $c-main;
    }
    &:nth-child(1),
    &:nth-child(2) {
      width: 50%;
    }
    &:nth-child(4),
    &:nth-child(5),
    &:nth-child(6) {
      width: 33%;
    }
  }
}
#addNewBox {
  border-top: 1px solid #eee;
  margin-top: 8px;
  padding-top: 8px;
  h4 {
    color: $c-main;
  }
  ol {
    list-style: normal inside;
    margin-bottom: 32px;
    li {
      color: $c-main;
    }
  }
  .submit {
    display: flex;
    justify-content: end;
    gap: 8px;
    button {
      border-radius: 4px;
      border: 1px solid;
      margin: 0;
      padding: 8px 16px;
      background: none;
      transition: all 0.2s;
      &:nth-child(1) {
        border-color: $c-main;
        color: #fff;
        background: $c-main;
        &:hover {
          background: $c-main * 0.9;
        }
      }
      &:nth-child(2) {
        border-color: #ccc;
        background-color: #fff;
        &:hover {
          color: #000;
          background-color: #eee;
        }
      }
    }
  }
}
